import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import register from './views/register.vue'
import login from './views/login.vue'
import meetings from './views/meetings.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/meetings',
      name: 'meetings',
      component: meetings
    },
    {
      path: '/register',
      name: 'register',
      component: register
    },
    {
      path: '*',
      redirect : '/'
    }
  ]
})
